html
  scroll-behavior: smooth
body
  // заменить ниже на свои значения
  @extend %body
  color: $black
  min-width: 320px
  &._lock
    overflow: hidden !important
    height: 100% !important

html,
body
  height: 100%
  margin: 0

ul, li
  padding: 0
h1,h2,h3
  margin: 0
input
  outline: none

.wrapper
  position: relative
  display: flex
  flex-direction: column
  min-height: 100%
  overflow: hidden

.main
  flex: 1 0 auto

.footer
  flex: 0 0 auto

.container
  max-width: 1105px
  margin: 0 auto
  padding: 0 15px

[class*='__container']
  max-width: $containerWidth + px
  margin: 0 auto
  padding: 0 $containerPadding + px

.image
  width: 100%
  height: 100%
  object-fit: cover

.logo--header
  height: 55px

.heading
  @extend %head

.btn-reset
  padding: 0
  border: none
  background-color: transparent
  cursor: pointer
  outline: none

.button
  position: relative
  display: inline-block
  padding: 18px 44px 18px 32px
  font-family: 'Gilroy', sans-serif
  font-weight: 800
  font-size: 20px
  line-height: 100%
  letter-spacing: 0.03em
  text-transform: uppercase
  color: #000000
  background-color: $orange
  border-radius: 100px
  transition: background-color 0.3s linear
  &::before
    content: ''
    position: absolute
    top: 50%
    right: 24px
    width: 12px
    height: 16px
    transform: translateY(-50%)
    background-image: url(../images/icons/sprite.svg#arrow-btn)
    background-position: 0 0
    background-repeat: no-repeat
    transition: right 0.3s linear

.unvisible
  position: absolute
  left: -3000px
  width: 0
  height: 0
  overflow: hidden

.grecaptcha-badge
  z-index: -10
  opacity: 0
  visibility: hidden

._ibg
  position: relative
  img
    position: absolute
    width: 100%
    height: 100%
    top: 0
    left: 0
    object-fit: cover
